import React, { useState } from "react";

import * as UFS from "./UserLayout/UserFormStyle";

import UserActionBtn from "./UserLayout/UserActionBtn";

export default function EditPassword() {
  const [oldPassword, setOldPassword] = useState("");
  const [newPassword, setNewPassword] = useState("");
  const [newPassword2, setNewPassword2] = useState("");
  return (
    <UFS.FormWrapper>
      <UFS.FormRow>
        <UFS.FormItem>
          <label htmlFor="oldPassword"></label>
          <input
            value={oldPassword}
            id="oldPassword"
            onChange={(e) => setOldPassword(e.target.value)}
            type="password"
          />
        </UFS.FormItem>
      </UFS.FormRow>
      <UFS.WarnningText>
        *6
      </UFS.WarnningText>
      <UFS.FormRow>
        <UFS.FormItem>
          <label htmlFor="newPassword"></label>
          <input
            value={newPassword}
            id="newPassword"
            onChange={(e) => setNewPassword(e.target.value)}
            type="password"
          />
        </UFS.FormItem>
      </UFS.FormRow>
      <UFS.FormRow>
        <UFS.FormItem>
          <label htmlFor="newPassword2"></label>
          <input
            value={newPassword2}
            id="newPassword2"
            onChange={(e) => setNewPassword2(e.target.value)}
            type="password"
          />
        </UFS.FormItem>
      </UFS.FormRow>
      <UFS.FormRow>
        <UserActionBtn />
      </UFS.FormRow>
    </UFS.FormWrapper>
  );
}
